<template>
  <div id="zhuye" >
    <yd-navbar :title="centerpoint.areaName"bgcolor="#0FA0FA" color="#fff"></yd-navbar>
    <div  class="mapcontent">
      <div id="myChart" style="width: 100%;height: 100%;overflow: hidden"></div>
    </div>
  </div>

</template>

<script>
  import { mapState } from 'vuex'
  let echarts = require('echarts/lib/echarts')
  require('echarts/extension/bmap/bmap');
export default {

  data () {
    return {
      mapdata: [
//          {name:"长沙",lng:120.54523,lat:30.642955},
//          {name:"长沙1",lng:120.52424,lat:30.647956},
//          {name:"长沙2",lng:120.52523,lat:30.642955},
//          {name:"长沙3",lng:120.56523,lat:30.642955},
//          {name:"长沙4",lng:120.54923,lat:30.645955},
//          {name:"长沙5",lng:120.56523,lat:30.648955},
//          {name:"长沙6",lng:120.57523,lat:30.644955},
//          {name:"长沙7",lng:120.54823,lat:30.646955},
//          {name:"长沙8",lng:120.53223,lat:30.647955},
//          {name:"长沙9",lng:120.56743,lat:30.641955},
//          {name:"长沙10",lng:120.54523,lat:30.648955},
//          {name:"长沙11",lng:120.52553,lat:30.648955},
//          {name:"长沙12",lng:120.53593,lat:30.646955},
        ],
      centerdata:{
      }
    }
  },
  computed:{
    ...mapState({
      centerpoint:"centerpoint"
    })
  },
  watch:{

  },
  mounted: function(){

    this.getpoints()

  },

  methods:{
    init:function () {
      let vm = this;
      console.log(vm.centerpoint);
      console.log(vm.mapdata);
      vm.centerdata.name=vm.centerpoint.centerName;
      vm.centerdata.lng=vm.centerpoint.centerLongitude;
      vm.centerdata.lat=vm.centerpoint.centerLatitude;
      vm.drawChinaMap(vm.centerdata.lng,vm.centerdata.lat,14);
    },
    getpoints:  function () {
      let vm = this;
      vm.api(vm,'post',"信息获取中...","common/company/selectPointListByCId.json",{centerId:vm.centerpoint.centerId},function (data) {
        console.log(data)
        if(data.statusCode == 0){
           for(let i=0;i<data.dataInfo.listData.length;i++){
            vm.mapdata.push({
                name:data.dataInfo.listData[i].companyName,
                lng:data.dataInfo.listData[i].pointLongitude,
                lat:data.dataInfo.listData[i].pointLatitude
              });
            vm.init()
           }
        }
      });
    },
//    处理数据
    convertData:function () {
       let vm = this;
       let res = [];
       for(let i=0;i<vm.mapdata.length;i++){
           res.push({
             name:vm.mapdata[i].name,
             value:[vm.mapdata[i].lng,vm.mapdata[i].lat]
           })
       }
       return res;
    },
//    初始化数据点 中心点经纬度 地图缩放层级
    drawChinaMap(lng,lat,leve) {
      let vm = this;
      var option = {
        title: {
          text: '',
        },
        tooltip : {
          trigger: 'item'
        },
        bmap: {
          center: [lng, lat],
          zoom:leve,
          roam: true,
        },
        series : [
          {
            name: '企业信息',
            type: 'scatter',
            coordinateSystem: 'bmap',
            data: vm.convertData(),
            symbolSize: 12,
            label: {
              normal: {
                formatter: '{b}',
                position: 'right',
                show: false
              },
              emphasis: {
                show: true
              }
            },
            itemStyle: {
              normal: {
                color: '#f66'
              }
            }
          },
          {
            name: '中心点',
            type: 'effectScatter',
            coordinateSystem: 'bmap',
            data: [{ name: "中心点",
              value:[lng,lat]}],
            symbolSize:20,
            showEffectOn: 'render',
            rippleEffect: {
              brushType: 'stroke'
            },
            hoverAnimation: true,
            label: {
              normal: {
                formatter: '{b}',
                position: 'right',
                show: true
              }
            },
            itemStyle: {
              normal: {
                color: '#f66',
                shadowBlur: 10,
                shadowColor: '#333'
              }
            },
            zlevel: 1
          }
        ]
      };
      let myChart = echarts.init(document.getElementById('myChart'))
      // 绘制图表
      myChart.setOption(option);
      var bmap = myChart.getModel().getComponent('bmap').getBMap();
      vm.drawline(bmap);
      vm.addControl(bmap,"第二象限",BMAP_ANCHOR_TOP_LEFT,10,10,function () {
        vm.topcontrol(2)
      });
      vm.addControl(bmap,"第一象限",BMAP_ANCHOR_TOP_RIGHT,10,10,function () {
        vm.topcontrol(1)
      });
      vm.addControl(bmap,"第四象限",BMAP_ANCHOR_BOTTOM_RIGHT,10,40,function () {
        vm.topcontrol(4)
      });
      vm.addControl(bmap,"第三象限",BMAP_ANCHOR_BOTTOM_LEFT,10,40,function () {
        vm.topcontrol(3)
      });
    },

    topcontrol:function (position) {
      this.$router.push({path:"companylist",query:{position:position,centerId:this.centerpoint.centerId}})
    },
//    根据中心点和半径（米）确认四个象限的直线点
//    经度每隔0.00001度，距离相差约1米；
//    纬度每隔0.00001度，距离相差约1.1米；   约等于 1米 为0.000009度
    getlinedata:function (lng,lat,radis) {
        let top = {lng:lng,lat:(lat+(radis*0.000009)).toFixed(5)}
        let bottom = {lng:lng,lat:(lat-(radis*0.000009)).toFixed(5)}
        let left = {lng:(lng-(radis*0.00001)).toFixed(5),lat:lat}
        let right = {lng:(lng+(radis*0.00001)).toFixed(5),lat:lat}
        let fourdot ={
          top:top,
          bottom:bottom,
          left:left,
          right:right
        };
        return fourdot

    },
//    给地图画十字线  根据中心点和半径 确认上下左右四个点坐标
    drawline:function (bmap) {
      let vm = this;
      let fourdot= vm.getlinedata(vm.centerdata.lng,vm.centerdata.lat,6000)
      var polyline = new BMap.Polyline([
          new BMap.Point(fourdot.top.lng, fourdot.top.lat),
          new BMap.Point(fourdot.bottom.lng, fourdot.bottom.lat)
        ],
        {strokeColor:"#f66", strokeWeight:1, strokeOpacity:0.5}
      );
      var polyline1 = new BMap.Polyline([
          new BMap.Point(fourdot.left.lng, fourdot.left.lat),
          new BMap.Point(fourdot.right.lng, fourdot.right.lat)
        ],
        {strokeColor:"#f66", strokeWeight:1, strokeOpacity:0.5}
      );
      bmap.addOverlay(polyline);
      bmap.addOverlay(polyline1);
    },
    addControl:function (map,content,off,top,left,fn) {
      // 定义一个控件类，即function
      function ZoomControl(){
        // 设置默认停靠位置和偏移量
        this.defaultAnchor = off;
        this.defaultOffset = new BMap.Size(top, left);
      }
// 通过JavaScript的prototype属性继承于BMap.Control
      ZoomControl.prototype = new BMap.Control();
      ZoomControl.prototype.initialize = function(map){
        // 创建一个DOM元素
        var div = document.createElement("div");
        // 添加文字说明
        div.appendChild(document.createTextNode(content));
        // 设置样式
        div.classList.add("Controlfour");
        div.style.cursor = "pointer";
        div.style.border = "1px solid #ccc";
        div.style.backgroundColor = "rgba(255,255,255,.5)";
        div.style.borderRadius = "3px";
        div.style.padding = "5px";
        div.style.boxShadow="1px 1px 2px rgba(0,0,0,.4)"
        // 绑定事件，点击一次放大两级
        div.onclick = fn
        // 添加DOM元素到地图中
        map.getContainer().appendChild(div);
        // 将DOM元素返回
        return div;
      }

// 创建控件实例
      var myZoomCtrl = new ZoomControl();
// 添加到地图当中
      map.addControl(myZoomCtrl);


  }

  }
}
</script>

<style scoped>
 #zhuye{
   display: flex;
  flex-direction: column;
   height: 100%;
   box-sizing: border-box;
 }
  .mapcontent{
    flex: 1;
    background-color: #fff;
    overflow: hidden;
  }
  #zhuye .Controlfour,.BMap_noprint,.anchorTL{
    background-color: #555;
    color: red;
  }
</style>



